

.main{
    flex: 1;
    color: white;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
}

.gridIcon{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.gridTitle{
    font-size: 23px;
    font-weight: bold;
    margin-top: 10px;
}

.gridInfo{
    border-radius: 10px;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    font-size: 14px;
    text-align: center;
    margin-top: 10px;
}

.yourImc{
    font-size: 17px;
    margin: 10px 0 50px 0;
}